import React from "react"
import { Link, FormattedMessage, injectIntl } from 'gatsby-plugin-intl'
import { graphql } from "gatsby"
import { Container, Row, Col, Jumbotron } from "react-bootstrap"
import Layout from '@components/layout'
import PostLink from "@components/element/postLink"
import HubSpot from '@components/block/hubspotForm'

const Press = ({
  data: {
    allMarkdownRemark: { edges },
  },
}) => {
  const Posts = edges
    .filter(edge => !!edge.node.frontmatter.date) // You can filter your posts based on some criteria
    .map(edge => <li className="mb-2" key={edge.node.id}><PostLink post={edge.node} /></li>)
  return (
    <Layout>
      <Jumbotron>
        <Container>
          <Row>
            <Col xs="12" md="10" lg="9" className="ml-auto mr-auto">
              <h1 className="display-2 my-4 py-4">
                <strong>Press mentions and updates along our journey.</strong>
              </h1>
              <p className="lead my-5 text-left">
                <strong>We are proud that others have found our work as interesting and exciting as us!</strong> Find here some nice articles and press mentions about us.
              </p>
              <div className="pt-4 text-center text-sm-left">
                <HubSpot title="Receive Updates" actionText="Subscribe for Updates" formId="713d7366-3f1a-4160-a2d0-d1ad9b015941" person="matthias" />
                <a href="https://www.linkedin.com/company/snapscreen/" target="_blank" rel="noopener noreferrer" className="btn btn-link btn-lg mx-sm-2 mt-2 mt-sm-0">
                Follow us on LinkedIn</a>
              </div>
            </Col>
          </Row>
        </Container>
      </Jumbotron>
      <section className="">
        <Container>
          <ul className="list-unstyled">{Posts}</ul>
        </Container>
      </section>
    </Layout>
  )
}

export default Press

export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          html
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            url
            teaser
            reference
            title
          }
        }
      }
    }
  }
`
